/* -------------------------------------------------------------------------- */
/* MARK: input/select */
/* -------------------------------------------------------------------------- */

input {
  height: 20px;
  accent-color: var(--primary);
}

select {
  height: 30px;
  font-size: 15px;
}

/* -------------------------------------------------------------------------- */
/* MARK: button */
/* -------------------------------------------------------------------------- */

.button {
  background-color: var(--primary);
  color: white;
  border: 1px solid black;
  border-radius: 3px;
  padding: 6px;
  margin-top: 4px;
  margin-right: 4px;
  margin-left: 4px;
  margin-bottom: 4px;
  transition:
    background 0.3s,
    transform 0.18s,
    box-shadow 0.3s;
  user-select: none; /* Prevent text selection */
}

.button:hover {
  background-color: var(--primary-darker);
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.087);
}

.button:active {
  transform: scale(0.92);
  box-shadow: 0 2px 5px rgba(69, 69, 70, 0.5);
}

.button.secondary {
  color: black;
  background-color: white;
}

/* -------------------------------------------------------------------------- */
/* MARK: table */
/* -------------------------------------------------------------------------- */

.table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  /* min-width: 400px; */
  /* box-shadow: 0 0 20px rgba(0, 0, 0, 0.15); */
  background-color: white;
}

.table tbody tr {
  border-bottom: 1px solid gray;
}

.table thead tr {
  text-align: left;
  font-weight: bold;
  border-bottom: 1px solid gray;
}

.table th,
.table td {
  padding: 12px 15px;
}

.table td:first-child,
.table th:first-child {
  border-right: 1px solid gray;
}

